import { useDesignSystemTheme } from '../../design-system/Hooks/useDesignSystemTheme';

type MessageBodyProps = {
  children: React.ReactNode;
};

const MessageBody = ({ children }: MessageBodyProps) => {
  const { getPrefixedClassName } = useDesignSystemTheme();

  const typographyClassname = getPrefixedClassName('typography');

  return (
    <div
      css={{
        [`& .${typographyClassname}:last-of-type`]: {
          marginBottom: 0,
        },
      }}
    >
      {children}
    </div>
  );
};

export default MessageBody;
