import { css } from '@emotion/react';

import { useDesignSystemTheme } from '../../design-system/Hooks/useDesignSystemTheme';
import { ChevronLeftIcon, ChevronRightIcon } from '../../design-system/Icon';
import { Typography } from '../../design-system/Typography';

type PaginationProps = {
  onPrevious: () => void;
  onNext: () => void;
  current: number;
  total: number;
};

const paginationStyles = {
  container: css({
    display: 'flex',
    alignItems: 'center',
    justifyItems: 'center',
    gap: 'var(--spacing-sm)',
  }),

  button: css({
    border: 'none',
    backgroundColor: 'transparent',
    padding: 0,
    display: 'flex',
    height: 'var(--spacing-md)',
    alignItems: 'center',
  }),
};

export const MessagePagination = ({ onPrevious, onNext, current, total }: PaginationProps) => {
  const { theme } = useDesignSystemTheme();

  return (
    <div
      css={paginationStyles.container}
      style={{
        ['--spacing-sm' as string]: `${theme.spacing.sm}px`,
        ['--spacing-md' as string]: `${theme.spacing.md}px`,
      }}
    >
      <button
        css={paginationStyles.button}
        onClick={onPrevious}
        style={{
          color: current === 1 ? theme.colors.actionDisabledText : theme.colors.textSecondary,
          cursor: current === 1 ? 'arrow' : 'pointer',
        }}
      >
        <ChevronLeftIcon />
      </button>
      <Typography.Text
        style={{
          color: theme.colors.textSecondary,
        }}
      >{`${current}/${total}`}</Typography.Text>
      <button
        css={paginationStyles.button}
        onClick={onNext}
        style={{
          color: current === total ? theme.colors.actionDisabledText : theme.colors.textSecondary,
          cursor: current === total ? 'arrow' : 'pointer',
        }}
      >
        <ChevronRightIcon />
      </button>
    </div>
  );
};

export default MessagePagination;
