import { css } from '@emotion/react';

import { useDesignSystemTheme } from '../../design-system/Hooks/useDesignSystemTheme';

type MessageProps = {
  isActiveUser: boolean;
  children: React.ReactNode;
};

const messageStyles = {
  container: css({
    display: 'flex',
    flexDirection: 'column',
    gap: 'var(--spacing-sm)',
    paddingTop: 'var(--spacing-md)',
    paddingRight: 'var(--spacing-sm)',
    paddingBottom: 'var(--spacing-md)',
    paddingLeft: 'calc(var(--spacing-md) * 2)',
    backgroundColor: 'var(--background)',
    borderBottom: `1px solid var(--border-decorative)`,
  }),
};

export const Message = ({ isActiveUser, children }: MessageProps) => {
  const { theme } = useDesignSystemTheme();

  return (
    <div
      css={messageStyles.container}
      style={{
        ['--spacing-sm' as string]: `${theme.spacing.sm}px`,
        ['--spacing-md' as string]: `${theme.spacing.md}px`,
        ['--background' as string]: isActiveUser ? theme.colors.backgroundPrimary : theme.colors.backgroundSecondary,
        ['--border-decorative' as string]: theme.colors.borderDecorative,
      }}
    >
      {children}
    </div>
  );
};

export default Message;
