import { css } from '@emotion/react';

import { useDesignSystemTheme } from '../../design-system/Hooks/useDesignSystemTheme';
import { Typography } from '../../design-system/Typography';

type MessageHeaderProps = {
  userName: React.ReactNode;
  avatarURL?: string;
  leftContent?: React.ReactNode;
  rightContent?: React.ReactNode;
};

const headerStyles = {
  container: css({
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    position: 'relative',
  }),

  leftContent: css({
    display: 'flex',
    alignItems: 'center',
    gap: 'var(--spacing-sm)',
  }),

  rightContent: css({
    display: 'flex',
    alignItems: 'center',
  }),

  avatar: css({
    position: 'absolute',
    left: -22,
    top: 2,
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    width: 'var(--spacing-md)',
    height: 'var(--spacing-md)',
    borderRadius: '50%',
  }),
};
const avatarDefaultBackgroundImage = 'linear-gradient(180deg, #FFD983 0%, #FFB800 100%)';

const MessageHeader = ({ userName, avatarURL, leftContent, rightContent }: MessageHeaderProps) => {
  const { theme } = useDesignSystemTheme();
  return (
    <div
      css={headerStyles.container}
      style={{
        ['--spacing-sm' as string]: `${theme.spacing.sm}px`,
        ['--spacing-md' as string]: `${theme.spacing.md}px`,
      }}
    >
      <div css={headerStyles.leftContent}>
        <div
          css={headerStyles.avatar}
          style={{
            backgroundImage: avatarURL
              ? `url(${avatarURL}), ${avatarDefaultBackgroundImage}`
              : avatarDefaultBackgroundImage,
          }}
        />
        <Typography.Text bold>{userName}</Typography.Text>
        {leftContent}
      </div>
      <div css={headerStyles.rightContent}>{rightContent}</div>
    </div>
  );
};

export default MessageHeader;
