import { css } from '@emotion/react';
import { useState } from 'react';

import { Button } from '../../design-system/Button';
import { useDesignSystemTheme } from '../../design-system/Hooks/useDesignSystemTheme';
import { CloseIcon, FaceFrownIcon, FaceNeutralIcon, FaceSmileIcon } from '../../design-system/Icon';
import { Typography } from '../../design-system/Typography';

type FeedbackProps = {
  onFeedback: (feedback: 'Better' | 'Same' | 'Worse') => void;
};

const feedbackStyles = {
  container: css({
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'flex-end',
  }),

  feedbackPrompt: css({
    display: 'flex',
    flexDirection: 'column',
    gap: 'var(--spacing-sm)',
  }),

  feedbackOptions: css({
    display: 'flex',
    gap: 'var(--spacing-sm)',
  }),
};

export const Feedback = ({ onFeedback }: FeedbackProps) => {
  const [isVisible, setIsVisible] = useState(true);
  const { theme } = useDesignSystemTheme();

  if (!isVisible) {
    return null;
  }

  return (
    <div
      css={feedbackStyles.container}
      style={{
        ['--spacing-sm' as string]: `${theme.spacing.sm}px`,
        ['--text-secondary' as string]: theme.colors.textSecondary,
      }}
    >
      <div css={feedbackStyles.feedbackPrompt}>
        <Typography.Text
          style={{
            color: theme.colors.textSecondary,
          }}
        >
          Was this response:
        </Typography.Text>
        <div css={feedbackStyles.feedbackOptions}>
          <Button
            icon={<FaceSmileIcon />}
            onClick={() => {
              onFeedback('Better');
              setIsVisible(false);
            }}
          >
            Better
          </Button>
          <Button
            icon={<FaceNeutralIcon />}
            onClick={() => {
              onFeedback('Same');
              setIsVisible(false);
            }}
          >
            Same
          </Button>
          <Button
            icon={<FaceFrownIcon />}
            onClick={() => {
              onFeedback('Worse');
              setIsVisible(false);
            }}
          >
            Worse
          </Button>
        </div>
      </div>
      <Button icon={<CloseIcon />} onClick={() => setIsVisible(false)} />
    </div>
  );
};

export default Feedback;
