import { css } from '@emotion/react';

import { useDesignSystemTheme } from '../../design-system/Hooks/useDesignSystemTheme';

type CodeSnippetProps = {
  language: string;
  buttons: React.ReactNode;
  children: React.ReactNode;
};

const snippetStyles = {
  container: css({
    margin: 'var(--spacing-md) 0',
  }),

  header: css({
    backgroundColor: 'var(--color-grey700)',
    color: 'var(--color-grey300)',
    borderRadius: 'var(--border-radius) var(--border-radius) 0 0',
    padding: 'var(--spacing-sm) var(--spacing-sm) var(--spacing-sm) var(--spacing-md)',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  }),

  buttonSvg: css({
    svg: {
      color: 'var(--color-grey300)',
    },
  }),

  code: css({
    padding: 'var(--spacing-sm) var(--spacing-md)',
    margin: '0',
    borderRadius: '0 0 var(--border-radius) var(--border-radius)',
    backgroundColor: 'var(--color-grey800)',
    color: 'var(--color-grey300)',
  }),
};

export const CodeSnippet = ({ language, buttons, children }: CodeSnippetProps) => {
  const { theme } = useDesignSystemTheme();

  return (
    <div
      css={snippetStyles.container}
      style={{
        ['--spacing-sm' as string]: `${theme.spacing.sm}px`,
        ['--spacing-md' as string]: `${theme.spacing.md}px`,
        ['--border-radius' as string]: `${theme.general.borderRadiusBase}px`,
        ['--color-grey700' as string]: theme.colors.grey700,
        ['--color-grey300' as string]: theme.colors.grey300,
        ['--color-grey800' as string]: theme.colors.grey800,
      }}
    >
      <div css={snippetStyles.header}>
        <span>{language}</span>
        <div css={snippetStyles.buttonSvg}>{buttons}</div>
      </div>
      <pre css={snippetStyles.code}>{children}</pre>
    </div>
  );
};

export default CodeSnippet;
