import { css } from '@emotion/react';
import { useEffect, useState } from 'react';

import type { ButtonProps } from '../../design-system/Button';
import { Button } from '../../design-system/Button';
import { useDesignSystemTheme } from '../../design-system/Hooks/useDesignSystemTheme';
import { CheckIcon } from '../../design-system/Icon';

const buttonStyles = {
  container: css({
    position: 'relative',
    display: 'inline-block',
    width: 'max-content',
  }),
  checkIcon: css({
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    pointerEvents: 'none',

    svg: {
      color: 'var(--checkmark-color)',
    },
  }),
};

const MessageActionButton = (props: ButtonProps) => {
  const [showCheck, setShowCheck] = useState(false);
  const { theme } = useDesignSystemTheme();

  const handleClick = (e: React.MouseEvent<HTMLElement>) => {
    setShowCheck(true);
    props.onClick?.(e);
  };

  if (props.children) {
    throw new Error('MessageActionButton: Children not supported; use as icon-only button.');
  }

  useEffect(() => {
    let timeoutId: NodeJS.Timeout;
    if (showCheck) {
      timeoutId = setTimeout(() => {
        setShowCheck(false);
      }, 750);
    }
    return () => {
      clearTimeout(timeoutId);
    };
  }, [showCheck]);

  return (
    <span
      css={buttonStyles.container}
      style={{
        ['--checkmark-color' as string]: theme.colors.green500,
      }}
    >
      <Button
        size="small"
        {...props}
        onClick={handleClick}
        css={{
          svg: {
            transition: showCheck ? 'none' : 'opacity 350ms',
            opacity: showCheck ? '0' : '1',
          },
        }}
      />
      <CheckIcon
        css={buttonStyles.checkIcon}
        style={{
          transition: showCheck ? 'none' : 'opacity 350ms',
          opacity: showCheck ? '1' : '0',
        }}
      />
    </span>
  );
};

export default MessageActionButton;
