import { css } from '@emotion/react';
import React from 'react';

import { Button } from '../../design-system/Button';
import { useDesignSystemTheme } from '../../design-system/Hooks/useDesignSystemTheme';
import { CursorIcon } from '../../design-system/Icon';
import { Input } from '../../design-system/Input';
import type { TextAreaProps } from '../../design-system/Input';

type ChatInputProps = {
  className?: string;
  textAreaProps?: TextAreaProps;
  onSubmit?: (value: string) => void;
  suggestionButtons?: React.ReactNode;
};

const chatInputStyles = {
  container: css({
    backgroundColor: 'var(--background-primary)',
    padding: 'var(--spacing-sm)',
    position: 'relative',
  }),

  suggestionButtons: css({
    display: 'flex',
    gap: 'var(--spacing-sm)',
    marginBottom: 'var(--spacing-sm)',
  }),

  textArea: css({
    minWidth: '100%',
    maxHeight: '150px !important',
  }),

  button: css({
    position: 'absolute',
    bottom: 'calc(var(--spacing-sm) + 4px)',
    right: 'calc(var(--spacing-sm) + 4px)',
    // Flip horizontally; the existing icon points to the left but this should be pointing to the right.
    transform: 'scaleX(-1)',
  }),
};

export const ChatInput = ({ className, onSubmit, textAreaProps, suggestionButtons }: ChatInputProps) => {
  const { theme } = useDesignSystemTheme();
  const [value, setValue] = React.useState('');

  const handleSubmit = () => {
    if (!value) {
      return;
    }

    onSubmit?.(value);
    setValue('');
  };

  return (
    <div
      css={chatInputStyles.container}
      style={{
        ['--spacing-sm' as string]: `${theme.spacing.sm}px`,
        ['--background-primary' as string]: theme.colors.backgroundPrimary,
        ['--border-decorative' as string]: theme.colors.borderDecorative,
      }}
      className={className}
    >
      {suggestionButtons && <div css={chatInputStyles.suggestionButtons}>{suggestionButtons}</div>}

      <Input.TextArea
        autoSize
        value={value}
        onChange={(e) => setValue(e.target.value)}
        placeholder="Send a message"
        css={chatInputStyles.textArea}
        onKeyDown={(e) => {
          if (e.key === 'Enter' && !e.metaKey && !e.ctrlKey && !e.shiftKey) {
            e.preventDefault();
            handleSubmit();
          }
        }}
        {...textAreaProps}
      />
      <Button size="small" css={chatInputStyles.button} icon={<CursorIcon />} onClick={handleSubmit} />
    </div>
  );
};

export default ChatInput;
